    float: left;
  }

  &-viewer {
    width: 90%;
    height: 100%;
    float: left;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  &-btn {
    border: none;
    outline: none;
    color: inherit;
    background-color: inherit;
    font-size: 0.8rem;
    color: #adadad;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    margin: 1rem;
    box-shadow: 0 1px 1px 1px rgba($color: #000000, $alpha: 0.2);
  }

  &-active {
    background-color: #2b2b2b;
    color: #fafafa;
  }
}
</style>