function parseAbility(ability, pokemon) {
    if (!ability.startsWith("Ability:")) return
    ability = ability.replace(/\s/g, "").split(":")[1].toLocaleLowerCase()
    pokemon.ability = ability
}

function parseLevel(level, pokemon) {
    if (!level.startsWith("Level:")) return
    level = level.replace(/\s/g, "").split(":")[1].toLocaleLowerCase()
    pokemon.level = Number(level)
}

function parseNameAndItem(part, pokemon) {
    let name = '', item
    part = part.replace(/\s/g, "").split("@")
    const nicknamed = part[0].indexOf("(")

    if (nicknamed > -1 && (part[0].slice(nicknamed, nicknamed + 3) !== "(M)" && part[0].slice(nicknamed, nicknamed + 3) !== "(F)")) {
        for (let i = nicknamed + 1; i < part[0].indexOf(")"); i++) {
            name += part[0][i].toLocaleLowerCase()
        }
    } else {
        if (part[0].includes("(")) {
            name = part[0].slice(0, part[0].indexOf("(")).toLocaleLowerCase()
        } else {
            name = part[0].toLocaleLowerCase()
        }
    }

    if (part[1] === undefined) {
        item = ""
    } else {
        item = part[1].toLocaleLowerCase().replace("-", "")
    }

    pokemon.name = name.replace("'", "")
    pokemon.item = item
}

function parseEvs(evs, pokemon) {
    pokemon.spread = [0, 0, 0, 0, 0, 0]
    const label = { hp: 0, atk: 1, def: 2, spa: 3, spd: 4, spe: 5 }

    evs = evs.split(":")
    evs[1].split(" / ").forEach(e => {
        const stat = e.replace(/[0-9' ']/g, '').toLocaleLowerCase()
        const val = Number(e.replace(/[^0-9]/g, ''))
        pokemon.spread[label[stat]] = val
    })
}

function parseIvs(ivs, pokemon) {
    pokemon.ivs = [31, 31, 31, 31, 31, 31]
    const label = { hp: 0, atk: 1, def: 2, spa: 3, spd: 4, spe: 5 }

    ivs = ivs.split(":")
    ivs[1].split(" / ").forEach(e => {
        const stat = e.replace(/[0-9' ']/g, '').toLocaleLowerCase()
        const val = Number(e.replace(/[^0-9]/g, ''))
        pokemon.ivs[label[stat]] = val
    })
}

function parseMoves(moves, pokemon) {
    pokemon.moves = []
    for (let i = 0; i < moves.length; i++) {
        const m = moves[i].replace(/[^a-zA-Z]/g, '')
        if (m !== "")
            pokemon.moves.push(m.toLocaleLowerCase())
    }
}

export const parseTeam = function (pokemonTeam) {
    let team = []
    let pkmn = pokemonTeam.split('\n\n')
    for (let i = 0; i < pkmn.length; i++) {
        let p = {}
        if (pkmn[i] < 3) {
            break
        }
        const pokeman = pkmn[i].split('\n')
        parseNameAndItem(pokeman[0], p)
        parseAbility(pokeman[1], p)
        let count = 2
        
        
        if (count < pokeman.length) {
            if (pokeman[count].startsWith("Level:")){
                parseLevel(pokeman[count], p)
                count++
            }
        }
        if (count < pokeman.length) {
            if (pokeman[count].startsWith("Shiny:")) count++
        }

        if (count < pokeman.length) {
            if (pokeman[count].startsWith("Happiness:")) count++
        }

        if (count < pokeman.length) {
            if (pokeman[count].startsWith("EVs:")) {
                parseEvs(pokeman[count], p)
                count++
            }
        }
        if (count < pokeman.length) {
            if (pokeman[count].includes("Nature")) {
                p.nature = pokeman[count].split(" ")[0].toLocaleLowerCase()
                count++
            }
        }

        if (count < pokeman.length) {
            if (pokeman[count].startsWith("IVs:")) {
                parseIvs(pokeman[count], p)
                count++
            }
        }

        parseMoves(pokeman.slice(count, pokeman.length), p)
        team.push(p)
    }
    return team
}