<template>
  <section v-if='!parsed' class='teamviewer'>
    <div class='lds-ellipsis'>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  </section>
  <TeamView class='teamviewer' v-else :parsed='parsed' />
</template>

<script>
import axios from "axios";
import { parseTeam } from "@/services/teamParser";
import TeamView from "@/components/TeamView";

export default {
  title: "Team Viewer",
  components: {
    TeamView
  },
  data() {
    return {
      parsed: false,
      tiers: {
        ou: 100,
        vgc: 50,
        bss: 50,
        ru: 100,
        nu: 100,
        ubers: 100,
        monotype: 100,
        uu: 100
      }
    };
  },
  created() {
    const id = this.$route.params.id;
    const tier = this.$route.params.tier;

    axios.post("/mysquad/shared", { id: id }).then(res => {
      if (res.data.code) {
        let parsed = {};
        parsed.pokemon = parseTeam(res.data.team.raw);
        parsed.tier = { defaultLevel: this.tiers[tier], alias: tier };
        parsed.raw = res.data.team.raw;

        for (let index in parsed.pokemon) {
          if (parsed.pokemon[index].level === undefined)
            parsed.pokemon[index].level = this.tiers[tier];
        }
        parsed.getData = true;
        axios.post("/mysquad", parsed).then(res => {
          if (res.data.sucess) {
            this.parsed = res.data.response;
          } else {
            alert("This team is no longer valid for some reason");
            this.$router.push({ name: "mysquad" });
          }
        });
      }
    });
  }
};
</script>

<style lang="scss" scoped>
.teamviewer {
  width: 65rem;
  height: 38rem;
}
</style>